ul {
    list-style: none;
  }

.homePage{
  height: 100%;
  width:100%;
}

.homepageHeader {
  height: 10%;
  position: relative;
  background: -webkit-gradient(linear, left top, right top, from(grey), to(rgb(212, 226, 208)));
  background: -webkit-linear-gradient(left, grey, rgb(212, 226, 208));
  background: -o-linear-gradient(left, grey, rgb(212, 226, 208));
  background: linear-gradient(to right, grey, rgb(212, 226, 208));
  border-bottom: 1px solid black;
  -webkit-box-shadow: 0px 5px 5px rgb(0, 61, 23), inset 0px 3px 3px rgb(27, 27, 27);
          box-shadow: 0px 5px 5px rgb(0, 61, 23), inset 0px 3px 3px rgb(27, 27, 27);
  text-align: left;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  padding: 0;
  margin: 0 0 0 0,
}

#menu-btn {
  position: relative;
  top:20%;
  left: 55%;
  background: rgb(0, 180, 0);
}

#main-chat-container {
  height: 100%;
  width: 100%;
}

#userInput {
  padding: 0 0 0 0;
  margin: 0 0 0 0;
}

#chat-form {
  position: relative;
  bottom: 0;
  height: 10%;
}

#send-btn {
  position: relative;
  background: rgb(0, 180, 0);
  bottom: 0;
  height: 10%;
}

#exit-chat {
  position: relative;
  background: rgb(180, 0, 0);

}

#userChatDisplay {
  position: relative;
  height:85%;
}

#converstaion {
  text-align: left;
  background: rgba(230, 230, 230, 0.719);
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  margin: auto auto;
}


